$green:  #25a00d;

.bg-about {
  background-color: #f1f5f0;
}
.btn {

  background-color: $green;
  border: 1px solid $green;
  box-shadow: 0 0 49px 10px rgba(15, 177, 34, .27), 0 3px 0 #186708;

  &_submit {
    background-color: $green;
  }
}

.container .owl-carousel .owl-nav button.owl-next,
.container .owl-carousel .owl-nav button.owl-prev {
  border: 1px solid $green;
}

.container .owl-carousel .owl-nav button.owl-next {
  background-image: linear-gradient(116deg, rgb(17,87,11) 0%, rgb(22,120,14) 100%);
}
.container .owl-carousel .owl-nav button.owl-prev:before {
  background: url("../resources/images/sprite-green.png") no-repeat;
  background-position: -120px -83px;
}
.owl_dots .active{
  & span {
    background-color: $green;
  }

  & span:after {
    border: 1px solid $green;
  }
}
.icon_svg {
  fill: $green;
}
